import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/PageWrapper"

const Gallery = ({ data }) => {
  console.log(data.allWordpressWpGallery.edges)
  return (
    <Layout>
      <h2>Galeria de Fotos</h2>
      <ul>
        {data.allWordpressWpGallery.edges.map(({ node: { title } }, index) => {
          return <li>{title}</li>
        })}
      </ul>
    </Layout>
  )
}

export default Gallery

export const query = graphql`
  query {
    allWordpressWpGallery {
      edges {
        node {
          slug
          title
          link
        }
      }
    }
  }
`
